import { ElementIdPrefixAndSuffix, ElementIds, HtmlTemplate } from "./TemplatingUtilities";
import { ObservedByNameTagSvg as NameTagSvgObservableParameters } from "./ObservedByNameTagSvg";

const parseFontSize = (fontSize: string) => {
	const trimmedFontSize = fontSize.trim();
	const magnitude = parseFloat(trimmedFontSize.slice(0, -2));
	const units = trimmedFontSize.slice(-2);
	return {magnitude, units};
}

const resizeSvgTextElementFactory = (element: SVGTextElement, maxTextWidth: number) => {
	const originalSize = parseFontSize(element.getAttribute("font-size")!);
	let currentFontSizeMagnitude = originalSize.magnitude;
	const resize = () => {
		const widthAtCurrentFontSize = element.getComputedTextLength();
		const adjustedSizeMagnitude = Math.min(originalSize.magnitude, currentFontSizeMagnitude * maxTextWidth / widthAtCurrentFontSize);
		if (adjustedSizeMagnitude != currentFontSizeMagnitude) {
			currentFontSizeMagnitude = adjustedSizeMagnitude;
			element.setAttribute("font-size", `${currentFontSizeMagnitude}${originalSize.units}`);
		}
	};
	return resize;
}

const simpleFormatSvgText = (simpleFormattedText: string): SVGTSpanElement[] => {
	let remainingText = simpleFormattedText;
	let startAt = 0;
	let formatting = {
		bold: false,
		italics: false,
		underline: false,
	};
	const tagActions: Record<string, (isClosingTag: boolean)=>void> = {
		"b": (isClosingTag) => { formatting.bold = !isClosingTag },
		"i": (isClosingTag) => { formatting.italics = !isClosingTag },
		"u": (isClosingTag) => { formatting.underline = !isClosingTag },
	};
	const resultTSpans: SVGTSpanElement[] = [];
	const dumpText = (textToDump: string) => {
		const tspan = document.createElementNS('http://www.w3.org/2000/svg', "tspan");
		tspan.textContent = textToDump;
		if (formatting.underline) {
			tspan.style.textDecoration = 'underline';
		};
		if (formatting.bold) {
			tspan.style.fontWeight = 'bold';
		};
		if (formatting.italics) {
			tspan.style.fontStyle = 'italic';
		};
		resultTSpans.push(tspan);
	}
	while (true) {
		let start = remainingText.indexOf('<', startAt);
		if (start < 0) {
			dumpText(remainingText);
			return resultTSpans;	
		}
		const isClosingTag = remainingText.substring(start + 1, start + 2) === "/";
		const end = remainingText.indexOf('>', start + 1);
		if (end < 0) {
			dumpText(remainingText);
			return resultTSpans;	
		}
		const tagName = remainingText.substring(start + (isClosingTag ? 2 : 1), end).toLocaleLowerCase();
		const action = tagActions[tagName];
		if (action != null) {
			if (start > 0) {
				dumpText(remainingText.substring(0, start));
			}
			action(isClosingTag);
			remainingText = remainingText.substring(end + 1);
			startAt = 0;
		} else {
			startAt = start + 1;
		}
	};
}

const NameTagSvgElementIdsSvg = [ 
	"nameTagSvg",
] as const;
const NameTagElementIdsSvgText = [
	"nameTagLine1SvgText",
	"nameTagLine2SvgText",
	"nameTagLine3SvgText",
	"nameTagLine4SvgText",
] as const;

type NameTagElements =
	Record<NonNullable<(typeof NameTagSvgElementIdsSvg)[number]>, SVGSVGElement> &
  Record<NonNullable<(typeof NameTagElementIdsSvgText)[number]>, SVGTextElement>;

const NameTagIds = [ 
		...NameTagSvgElementIdsSvg,
		...NameTagElementIdsSvgText,
	] as const satisfies readonly (keyof NameTagElements)[];

const width = 9; // name tag standard is 9 cm;
const height = 9; // name tag standard is 5.8

const html = String.raw;
const getNameTagSvg = (ids: ElementIds<NameTagElements>) => html`
<label class="name-tag-preview-label">your name tag will look like this</label>
<svg id="${ids.nameTagSvg}" viewBox="0 0 ${width} ${height}" style="width: ${width}cm; height: ${height}cm; font-family: sans-serif;">
	<rect x="0" y="0" height="${height}" width="${width}" style="fill: rgba(64, 0, 0, 1);"></rect>
	<rect x=".1" y=".1" height="${height-.2}" width="${width-.2}" style="fill: white;"></rect>
	<text id="${ids.nameTagLine1SvgText}" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="1.7" font-size="1.75pt" style="font-weight: bold;"></text>
	<text id="${ids.nameTagLine2SvgText}" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="3" font-size="0.8pt"></text>
	<text id="${ids.nameTagLine3SvgText}" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="4.35" font-size="0.6pt"></text>
	<text id="${ids.nameTagLine4SvgText}" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="5.35" font-size="0.45pt"></text>
	<rect x="0.1" y="${height-2.9}" height="2.8" width="${width-.2}" style="fill: rgba(64, 0, 0, 1);"></rect>
	<text id="logoPlaceholder" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="${height-2}" font-size="0.85pt" style="font-weight: bold; fill: white;">
		<tspan>Event Branding</tspan>
	</text>
	<text id="logoPlaceholder" dominant-baseline="middle" text-anchor="middle" x="${width/2}" y="${height-0.8}" font-size="0.85pt" style="font-weight: bold; fill: white;">
		<tspan>Belongs Here</tspan>
	</text>
</svg>
`;

const nameTagTextMaxWidth = 8;
export class NameTagSvg extends HtmlTemplate<NameTagElements> {
	constructor(containerElement: Element, public readonly observes: NameTagSvgObservableParameters, elementIdPrefixAndSuffix?: ElementIdPrefixAndSuffix) {
		super(containerElement, getNameTagSvg, NameTagIds, elementIdPrefixAndSuffix)		
		for (const [lineSvgText, lineObserved] of [
			[this.elements.nameTagLine1SvgText, this.observes.nameTagLine1],
			[this.elements.nameTagLine2SvgText, this.observes.nameTagLine2],
			[this.elements.nameTagLine3SvgText, this.observes.nameTagLine3],
			[this.elements.nameTagLine4SvgText, this.observes.nameTagLine4],
		] as const) {
			const resizeNameTagLine = resizeSvgTextElementFactory(lineSvgText, nameTagTextMaxWidth);
			lineObserved.listen(lineText => {
				lineSvgText.replaceChildren(...simpleFormatSvgText(lineText));
				resizeNameTagLine();
			});
		}
	}
}
