import { ElementIds, HtmlTemplate } from "./TemplatingUtilities";
import { ObservableComputation, ObservableHTMLInputElementValue, ObservableValue } from "./Observable"
import { ElementIdPrefixAndSuffix } from "./TemplatingUtilities";
import { ObservedByNameTagSvg } from "./ObservedByNameTagSvg";

const OpennessToTheyPronouns = {
	welcome: 'welcome',
	prefer: 'prefer',
	decline: 'decline',
} as const;
type OpennessToTheyPronouns = NonNullable<(typeof OpennessToTheyPronouns)[keyof typeof OpennessToTheyPronouns]>;

const RegistrationFormElementIdsInputElement = [
  "affiliateTextInputElement",
  "nameTextInputElement",
  "nameTagLine1TextInputElement",
  "nameTagLine2TextInputElement",
  "nameTagLine3TextInputElement",
  "nameTagLine4TextInputElement",
  "preferredThirdPersonPronounTextInputElement",
	"opennessToTheyPronounsWelcome",
//	"opennessToTheyPronounsPrefer",
	"opennessToTheyPronounsDecline",
	"opennessToTheyPronounsWelcomeLabel",
	"opennessToTheyPronounsPreferLabel",
	"opennessToTheyPronounsDeclineLabel",
	"opennessToTheyPronounsRadioButtonContainer",
] as const;

type RegistrationFormElements = Record<NonNullable<(typeof RegistrationFormElementIdsInputElement)[number]>, HTMLInputElement>;

const RegistrationFormElementIds = [
	...RegistrationFormElementIdsInputElement,
] as const satisfies readonly (keyof RegistrationFormElements)[];

const html = String.raw;
const getRegistrationFormHtml = (ids: ElementIds<RegistrationFormElements>) => html`
<div class="registration-form">
  <div class="form-input-container">
    <label for="${ids.nameTextInputElement}">name</label>
    <input type="text" size="30" id="${ids.nameTextInputElement}" placeholder="" />
  </div>
  <div class="form-input-container">
    <label for="${ids.affiliateTextInputElement}">affiliations(s)</label>
    <input type="text" size="30" id="${ids.affiliateTextInputElement}" />
  </div>
  <div class="form-input-container">
    <label for="${ids.preferredThirdPersonPronounTextInputElement}">preferred pronouns</label>
    <input type="text" id="${ids.preferredThirdPersonPronounTextInputElement}" placeholder="they/them" />
  </div>
	<div id="${ids.opennessToTheyPronounsRadioButtonContainer}" class="radio-group-container">
		<div class="radio-element-and-label-container">
			<input type="radio" name="opennessToTheyPronouns" value="welcome" id="${ids.opennessToTheyPronounsWelcome}" />
			<label id="${ids.opennessToTheyPronounsWelcomeLabel}" for="${ids.opennessToTheyPronounsWelcome}">I welcome gender-neutral (<i>they</i>) pronouns.<br/>(this promotes inclusivity)</label>
		</div>` +
/*		<div class="radio-element-and-label-container">
			<input type="radio" name="opennessToTheyPronouns" value="prefer" id="${ids.opennessToTheyPronounsPrefer}" />
			<label id="${ids.opennessToTheyPronounsPreferLabel}" for="${ids.opennessToTheyPronounsPrefer}">Add <i>they</i> to my preferred pronouns.<br/>(this further promotes inclusivity)</label>
		</div>
*/ `
		<div class="radio-element-and-label-container">
			<input type="radio" name="opennessToTheyPronouns" value="decline" id="${ids.opennessToTheyPronounsDecline}" />
			<label id="${ids.opennessToTheyPronounsDeclineLabel}" for="${ids.opennessToTheyPronounsDecline}">I prefer my pronouns only.</label>
		</div>
	</div>
  <div class="form-input-container">
    <label for="${ids.nameTagLine1TextInputElement}">name tag contents</label>
    <input type="text" size="30" id="${ids.nameTagLine1TextInputElement}"/>
    <input type="text" size="30" id="${ids.nameTagLine2TextInputElement}"/>
    <input type="text" size="30" id="${ids.nameTagLine3TextInputElement}"/>
    <input type="text" size="30" id="${ids.nameTagLine4TextInputElement}"/>
  </div>
</div>
`;


export class RegistrationForm extends HtmlTemplate<RegistrationFormElements> implements ObservedByNameTagSvg {
	nameTextInputElement: ObservableHTMLInputElementValue;
	get name() { return this.nameTextInputElement.value.trim(); }

	affiliateTextInputElement: ObservableHTMLInputElementValue;

	preferredThirdPersonPronounTextInputElement: ObservableHTMLInputElementValue;

	opennessToTheyPronouns = new ObservableValue<OpennessToTheyPronouns>(OpennessToTheyPronouns.welcome);

	nameTagLine1ObservableTextInputElement: ObservableHTMLInputElementValue;
	nameTagLine2ObservableTextInputElement: ObservableHTMLInputElementValue;
	nameTagLine3ObservableTextInputElement: ObservableHTMLInputElementValue;
	nameTagLine4ObservableTextInputElement: ObservableHTMLInputElementValue;
	nameTagLine1TextInputElementOverride = new ObservableValue<boolean>(false);
	nameTagLine2TextInputElementOverride = new ObservableValue<boolean>(false);
	nameTagLine3TextInputElementOverride = new ObservableValue<boolean>(false);
	nameTagLine4TextInputElementOverride = new ObservableValue<boolean>(false);

	preferredThirdPersonPronounsFromInputText = new ObservableComputation( (): string[] => {
		const reSpaceSlashOrCommaSequence = /[\s\/,]+/;
		return this.preferredThirdPersonPronounTextInputElement.value.split(reSpaceSlashOrCommaSequence)
			.map(s => s.trim())
			.filter(x => x.length > 0);
	});

	preferredThirdPersonPronounsFromInputTextSeparatedBySlash = new ObservableComputation( () =>
		this.preferredThirdPersonPronounsFromInputText.value.join("/")
	);

	preferredThirdPersonPronounsFromInputTextContainsThey = new ObservableComputation( () =>
		this.preferredThirdPersonPronounsFromInputText.value.find( pronoun => pronoun.toLocaleLowerCase() === "they") != null
	);

	preferredThirdPersonPronouns = new ObservableComputation( (): string[] =>
		this.preferredThirdPersonPronounsFromInputText.value.length === 0 ?
			["they", "them"] :
			[
				...this.preferredThirdPersonPronounsFromInputText.value,
				...((this.opennessToTheyPronouns.value === OpennessToTheyPronouns.prefer && this.preferredThirdPersonPronounsFromInputTextContainsThey.value === false) ? ["they"] : [])
			]
	);

	myOwnThirdPartyPronouns = new ObservableComputation( (): string =>
		this.preferredThirdPersonPronounsFromInputText.value.length > 0 ?
			this.preferredThirdPersonPronounsFromInputText.value.join('/') :
			'my own pronouns'
	);

	theyIsAmongPreferredThirdPersonPronouns = new ObservableComputation( (): boolean => {
		return this.preferredThirdPersonPronouns.value.find(pronoun => pronoun.toLocaleLowerCase() === "they") != null;
	});

	firstName = new ObservableComputation( () =>
		this.nameTextInputElement.value.trim().split(' ').filter( s => !s.endsWith('.'))[0] ?? ""
	)

	nameTagLine1TextInputElementPlaceholder = new ObservableComputation( () =>
		this.firstName.value
	)
	nameTagLine1 = new ObservableComputation( () =>
		this.nameTagLine1ObservableTextInputElement.value.length > 0 ? this.nameTagLine1ObservableTextInputElement.value : this.nameTagLine1TextInputElementPlaceholder.value
	)

	nameTagLine2TextInputElementPlaceholder = new ObservableComputation( () => {
		const name = this.nameTextInputElement.value;
		const line1 = this.nameTagLine1.value;
		if (name.startsWith(line1)) {
			return name.slice(line1.length).trim();
		} else {
			return name;
		}
	})
	nameTagLine2 = new ObservableComputation( () =>
		this.nameTagLine2ObservableTextInputElement.value.length > 0 ? this.nameTagLine2ObservableTextInputElement.value : this.nameTagLine2TextInputElementPlaceholder.value
	)

	nameTagLine3TextInputElementPlaceholder = new ObservableComputation( () =>
		this.affiliateTextInputElement.value
	)
	nameTagLine3 = new ObservableComputation( () =>
		this.nameTagLine3ObservableTextInputElement.value.length > 0 ? this.nameTagLine3ObservableTextInputElement.value : this.nameTagLine3TextInputElementPlaceholder.value
	)

	nameTagLine4TextInputElementPlaceholder = new ObservableComputation( () => {
		const shouldAppendTheWordsOrThey =
			this.opennessToTheyPronouns.value === OpennessToTheyPronouns.welcome &&
			!this.theyIsAmongPreferredThirdPersonPronouns.value;
		const ShouldAppendTheWordOnly = this.opennessToTheyPronouns.value === OpennessToTheyPronouns.decline && !this.theyIsAmongPreferredThirdPersonPronouns.value;
		return `${this.preferredThirdPersonPronouns.value.join('/')}${
			shouldAppendTheWordsOrThey ? ` <i>or they</i>` : ``
		}${
			ShouldAppendTheWordOnly ? ` <b>only</b>` : ``
		}`;
	});
	nameTagLine4 = new ObservableComputation( () =>
		this.nameTagLine4ObservableTextInputElement.value.length > 0 ? this.nameTagLine4ObservableTextInputElement.value : this.nameTagLine4TextInputElementPlaceholder.value,
	)

	showOpennessToThey = new ObservableComputation( () => 
		this.preferredThirdPersonPronounsFromInputText.value.length > 0 && this.preferredThirdPersonPronounsFromInputTextContainsThey.value === false
	);

	constructor(containerElement: Element, elementIdPrefixAndSuffix?: ElementIdPrefixAndSuffix) {
		super(containerElement, getRegistrationFormHtml, RegistrationFormElementIds, elementIdPrefixAndSuffix);

		this.nameTextInputElement = new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.nameTextInputElement});
		this.affiliateTextInputElement = new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.affiliateTextInputElement});
		this.preferredThirdPersonPronounTextInputElement = new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.preferredThirdPersonPronounTextInputElement});

		this.myOwnThirdPartyPronouns.listen( myOwnThirdPartyPronouns => {
			this.elements.opennessToTheyPronounsDeclineLabel.textContent = `I prefer ${myOwnThirdPartyPronouns} only.`;
		});

		const opennessToTheyPronounsRadioButtons = [
			[this.elements.opennessToTheyPronounsDecline, this.elements.opennessToTheyPronounsDeclineLabel, OpennessToTheyPronouns.decline],
			[this.elements.opennessToTheyPronounsWelcome, this.elements.opennessToTheyPronounsWelcomeLabel, OpennessToTheyPronouns.welcome],
//			[this.elements.opennessToTheyPronounsPrefer, this.elements.opennessToTheyPronounsPreferLabel, OpennessToTheyPronouns.prefer],
		] as const;

		this.showOpennessToThey.listen( showOpennessToThey => {
			opennessToTheyPronounsRadioButtons.forEach( ([inputElement, labelElement]) => {
				if (showOpennessToThey) {
					inputElement.removeAttribute('disabled');
					labelElement.style.removeProperty('color');
				} else {
					inputElement.setAttribute('disabled','');
					labelElement.style.setProperty('color', 'rgba(128,128,128,0.5)');
				}
	
			})
//			this.elements.opennessToTheyPronounsRadioButtonContainer.style.setProperty('visibility', showOpennessToThey ? 'visible' : 'hidden');
			if (showOpennessToThey) {
				this.elements.opennessToTheyPronounsRadioButtonContainer.removeAttribute('disabled');
			} else {
				this.elements.opennessToTheyPronounsRadioButtonContainer.setAttribute('disabled','');
			}
		});

		opennessToTheyPronounsRadioButtons.forEach( ([radioElement, labelElement, elementValue]) => {
			radioElement.addEventListener('change', () => { this.opennessToTheyPronouns.value = elementValue });
			labelElement.addEventListener('click', () => { this.opennessToTheyPronouns.value = elementValue });
			this.opennessToTheyPronouns.listen( opennessToTheyPronouns => {
				if (elementValue === opennessToTheyPronouns) {
					radioElement.setAttribute('checked', '');
				} else {
					radioElement.removeAttribute('checked');
				}
			});
		});

		this.nameTagLine1ObservableTextInputElement= new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.nameTagLine1TextInputElement});
		this.nameTagLine2ObservableTextInputElement= new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.nameTagLine2TextInputElement});
		this.nameTagLine3ObservableTextInputElement= new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.nameTagLine3TextInputElement});
		this.nameTagLine4ObservableTextInputElement= new ObservableHTMLInputElementValue({initialValue: "", element: this.elements.nameTagLine4TextInputElement});

		for (const [placeholder, input, override] of [
			[this.nameTagLine1TextInputElementPlaceholder, this.nameTagLine1ObservableTextInputElement, this.nameTagLine1TextInputElementOverride],
			[this.nameTagLine2TextInputElementPlaceholder, this.nameTagLine2ObservableTextInputElement, this.nameTagLine2TextInputElementOverride],
			[this.nameTagLine3TextInputElementPlaceholder, this.nameTagLine3ObservableTextInputElement, this.nameTagLine3TextInputElementOverride],
			[this.nameTagLine4TextInputElementPlaceholder, this.nameTagLine4ObservableTextInputElement, this.nameTagLine4TextInputElementOverride],
		] as const) {
			placeholder.listen( lineText => {
				input.element.setAttribute('placeholder', lineText ?? "");
				if (!override.value) {
					input.value = lineText;
				}
			});
			input.element.addEventListener('blur', () => {
				override.value = input.value.length > 0 && input.value !== placeholder.value;
			})
		}

	}
}


