export interface ElementIdPrefixAndSuffix {
  prefix?: string;
  suffix?: string;
}
const getElementIdsFactory = <T extends readonly string[]>(elementBaseIds: T) => ({ prefix = "", suffix = "" }: ElementIdPrefixAndSuffix = {}) => elementBaseIds.reduce((elementNames, elementName) => {
  elementNames[elementName as T[number]] = prefix + elementName + suffix;
  return elementNames;
}, {} as Record<T[number], string>
);
const getElementsFactory = <T extends Record<string, HTMLOrSVGElement>>(elementBaseIds: readonly (keyof T)[]) => ({ prefix = "", suffix = "" }: ElementIdPrefixAndSuffix = {}) => elementBaseIds.reduce((elements, elementName) => {
  Object.assign(elements, { [elementName]: document.getElementById(`${prefix}${elementName as string}${suffix}`) });
  return elements;
}, {} as T
) as T;

export const elementIdFunctions = <T extends Record<string, HTMLOrSVGElement>>(elementBaseIds: readonly (keyof T)[], prefixAndSuffix?: ElementIdPrefixAndSuffix) => {
  const getElementIds = getElementIdsFactory(elementBaseIds as readonly (string & keyof T)[]);
  const getElements = getElementsFactory(elementBaseIds);
  return {getElementIds, getElements};
}

export const appendHtmlToElement = (element: Element, html: string) => {
	const tempDiv = document.createElement('div');
	tempDiv.innerHTML = html;
	while (tempDiv.children.length > 0) {
		const firstChild = tempDiv.firstChild!;
		tempDiv.removeChild(firstChild);
		element.appendChild(firstChild);
	}
};

export type ElementIds<T extends Record<string, HTMLOrSVGElement>> = Record<string & keyof T, string>;

export class HtmlTemplate<T extends Record<string, HTMLOrSVGElement>> {
  readonly elementIds: ElementIds<T>;
  readonly elements: T;

  constructor(
    containerElement: Element,
    generateHtml: (elementIds: ElementIds<T>) => string, 
    elementBaseIds: readonly (keyof T)[],
    elementIdPrefixAndSuffix?: ElementIdPrefixAndSuffix
  ) {
    const {getElementIds, getElements} = elementIdFunctions<T>(elementBaseIds);
    this.elementIds = getElementIds(elementIdPrefixAndSuffix);
		appendHtmlToElement(containerElement, generateHtml(this.elementIds));
    this.elements = getElements(elementIdPrefixAndSuffix);
  }

}