import { RegistrationForm } from "./RegistrationForm";
import { NameTagSvg } from "./NameTagSvg";

document.addEventListener('DOMContentLoaded', () => {
	const rootElement = document.querySelector("[data-registration-form-here]");
	if (rootElement == null) return;
	const form = new RegistrationForm(rootElement);
	new NameTagSvg(rootElement, form);
});
